.App {
    text-align: left;
    padding: 20px;
    margin: 10px;
    font-family: 'Roboto', sans-serif;
    height: 100%;
    background-color: rgb(27, 73, 158);
    
  }
  h2 {
    text-align: center;
    color: white;
  }
  h3{
    text-align: center;
    color: white
  }


a{
  text-decoration: none !important;
  color: white;
  font-weight: 30;
  font-style: italic;

}

  button{
    position: inherit;
    border: 1px solid black;
    border-radius: 10%;
    background-color: Transparent;
    text-color: black;
    cursor:pointer;
    overflow: hidden;
    margin: 15px
  }

  
  .board{
    
    display: grid;
    grid-template-columns : repeat(3, 1fr);
    margin: 20px;
    border-radius: 2%;
    
    
  }
 
  .each-list{

    gap: 10px;
    border: 10px black;
    display: grid;
    grid-template-rows : 12% 88%;
    border-radius: 1%;
    margin: 15px;
    background-color: rgb(240, 136, 68)
    
  }
  
 
  .draggable{
     
    /* border: 1.5px black solid; */
    box-shadow: 5px 5px 15px rgb(5, 51, 33);
    text-align: left;
    margin: 20px;
    padding: 30px;
    border-radius: 4%;
    background-color: rgb(236, 214, 211)
    
  }

  .material-icons{
    color:red;
    float: left;
    height: 100%;
  }
  


  